import React from 'react';

const icons = {
  hot: {
    src: require('@/assets/img/hot.png').default,
    height: 22,
  },
  free: {
    src: require('@/assets/img/free.png').default,
    height: 17,
  },
  rec: {
    src: require('@/assets/img/rec.png').default,
    height: 14,
  },
};

function Tags({tag}) {
  const icon = icons[tag];
  return (
    <>
      {
        icon &&
        <img
          {...icon}
        />
      }
    </>
  );
}
export default Tags;
