import React from 'react';
import { useLocation, useNavigate, Link } from 'react-router-dom';
import { Progress } from 'antd';
import styles from '../styles.module.scss';
import Tags from "./tags";

const headerContList = [
  {
    key: 0,
    num: 50,
    color: '#09ac25',
    tit: '',
    cont: '',
  },
  {
    key: 1,
    num: 90,
    color: '#6c0fa5',
    tit: '',
    cont: '+',
  },
  {
    key: 2,
    num: 100,
    color: '#e2270c',
    tit: '',
    cont: '100',
  },
];
function Nav({routers}) {
  const location = useLocation();
  const navigate = useNavigate();
  const pathname = location.pathname;
  return (
    <header>
      <div className={styles.top}>
        <div className={styles['top-left']}>
          <div className={styles.logo}>
            <img
              src={require('@/assets/img/logo320.png').default}
              alt=""
            />
          </div>
          <div className={styles["title-box"]}>
            <div className={styles["big-title"]}>
              <h1></h1>
              <p></p>
            </div>
            <div className={styles["tit-desc"]}>
              <p>QQ387454101</p>
              <p>380</p>
            </div>
          </div>
        </div>
        <ul className={styles["header-cont"]}>
          {
            headerContList.map((item) => (
              <li key={item.key}>
                <Progress
                  type="circle"
                  percent={item.num}
                  format={(percent) => (
                    <span
                      style={{color: item.color}}
                    >
                    {`${percent}%`}
                  </span>
                  )}
                  size={64}
                  strokeColor={item.color}
                />
                <div className={styles["cont"]}>
                  <h2>{item.tit}</h2>
                  <p>{item.cont}</p>
                </div>
              </li>
            ))
          }
        </ul>
      </div>
      <hr/>
      <div className={styles.nav}>
        <ul>
          {routers.filter(r => !r.meta.hidden).map(router => (
            <li
              className={router.path === pathname ? styles.active : ''}
              key={router.key}
              onClick={() => navigate(router.path)}
            >
              <span>{router.meta.title}</span>
              {router.meta.tag && (
                <Tags tag={router.meta.tag} />
              )}
            </li>
          ))}
        </ul>
        <div></div>
      </div>
      <div className={styles.slogan}>
        
      </div>
    </header>
  );
}
export default Nav;
