import React from 'react';
import {
  Routes,
  Route,
} from 'react-router-dom';
import routers from './config';

function Router() {
  console.log('test router', routers);
  return (
    <Routes>
      {routers.map(router => (
        <Route
          key={router.key}
          path={router.path}
          element={<router.component />}
        />
      ))}
    </Routes>
  );
}
export default Router;
