  --color-background-code: #f2f2f2;

  --box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.1);

  /* Layout */
  --height-header: 3rem;
  --width-sidebar: 18rem;
  --width-toc: 12rem;

  /* Typography */
  --font-size: 16px;
  --font-size-small: 85%;

  --line-height: 1.5;
  --line-height-small: 1.25;

  --font-weight-bold: 600;
  /* Safari for OS X and iOS (San Francisco) */
  /* Chrome for OS X (San Francisco) */
  /* Chinese font for OSX and iOS */
  /* Android */
  /* Windows */
  /* Chinese font for Windows */
  /* Linux */
  /* Basic web fallback */
  /* Emoji */
  --font-family: '', 'Microsoft Yahei', 'Helvetica', 'Arial', '-apple-system', 'BlinkMacSystemFont', 'Hiragino Sans GB', 'Roboto', 'Segoe UI',
  'Oxygen-Sans', 'Ubuntu', 'Cantarell', 'STHeiti', sans-serif, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol';

  --font-family-monospace: 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace;

  /* z-index */
  --z-index-sidebar: 100;
  --z-index-header: 200;
  --z-index-popover: 300;

  /* Others */
  --opacity: 0.7;
  --transition-duration: 0.3s;
}

